.home {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
    background-color: #1A1A1D; /* Dark background */
    color: white; /* White text */
    height: calc(100vh - 60px); /* Assuming your nav bar is 60px in height */
  }
  
  .content h1 {
    font-size: 2.5rem; /* Large header size */
    color: #E6D5B8; /* Light header color */
    margin-bottom: 20px;
  }
  
  .content p {
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .content a {
    color: #E6D5B8; /* Light link color */
    text-decoration: none;
  }
  
  .content a:hover {
    text-decoration: underline;
  }
  
  .profile-image {
    width: 200px; /* Profile image size */
    height: 200px;
    border-radius: 50%;
    background-image: url('../assets/Prof_pic.png'); /* Path to your profile image */
    background-size: cover;
    background-position: center;
  }
  
  /* Responsive design adjustments */
  @media (max-width: 768px) {
    .home {
      flex-direction: column;
      text-align: center;
    }
  
    .profile-image {
      margin-bottom: 20px;
    }
  }
  